import React, { useContext, useState } from "react";
import { ShopContext } from "../../context/shop-context";
import { PRODUCTS } from "../../products";
import { CartItem } from "./cart-item";
import { Link } from "react-router-dom";
import Button from 'react-bootstrap/Button';
import { toast } from 'react-toastify';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import LoginForm from './LoginForm';



export const Cart = () => {
  const { cartItems, getTotalCartAmount, checkout } = useContext(ShopContext);
  const totalAmount = getTotalCartAmount();
  const [state, setState] = useState(true);
  const checkoutna = () => {
    setState(!state);
  };

  return (
    <div className="text-center">
      <div className="py-5">
        <h1>Your Cart Items</h1>
      </div>
      <div>
        {PRODUCTS.map((product) => {
          if (cartItems[product.id] !== 0) {
            return <CartItem data={product} />;
          }
        })}
      </div>

      {totalAmount > 0 ? (
        <Container fluid>
        <Row>
          <Col>
          <p style={{textAlign: 'justify'}} className="mt-5">Is this a gift? Please let us know if you would like your items wrapped. 
            If you have multiple items in an order, please note they will be bundled together unless otherwise specified in the notes to wrap items individually. 
            If you would like a message included with your gift, simply add it in the box below. 
            Please click on the "Update" button once completed to ensure your message is saved.
            </p>
            <InputGroup>
        <Form.Control as="textarea" aria-label="With textarea" />
      </InputGroup>
            </Col>
          <Col><div className="pt-5">
          <p className="fs-4 text-end me-5"> Subtotal <span className="ms-5 ps-5">  {totalAmount}</span></p>
          <p className="fs-4 text-end me-5"></p>
          <p className="fs-4 text-end me-5">Tax included and shipping calculated at checkout</p>
          <div class="d-flex flex-row-reverse gap-4">
          <LoginForm />
          <Button className="bg-dark w-25 fw-bolder  mb-5 border" style={{ height: '50px'}} onClick={checkoutna}><Link to="/shop" className='text-decoration-none text-light'>Continue Shopping</Link></Button>
          </div>
        </div></Col>
        </Row>
      </Container>
        
        
      ) : (
        <h1> Your Shopping Cart is Empty</h1>
      )}
    </div>
  );
};