
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import Row from 'react-bootstrap/Row';
import { Formik } from 'formik';
import * as yup from 'yup';
import { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import Modal from 'react-bootstrap/Modal';
import { Link } from "react-router-dom";


function FormExample() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const history = useHistory();
  const [datas, setDatas] = useState(null)
  console.log(datas);
  useEffect(() => {
    fetch('https://sheet.best/api/sheets/161c399f-bdd1-4864-a369-a92dacca92f1')
      .then(res => res.json())
      .then(json => setDatas(json))
      .then(err => {
        console.log(err)
      })
  }, []);
  
  const schema = yup.object().shape({
    email: yup.string().email('Enter a valid email').required('Email is required'),
    password: yup.string().required('Password is required')
  });

  return (
    <>
    <Button className=' w-25 bg-dark fw-bolder border' style={{ height: '50px'}} onClick={handleShow}>
        Checkout
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
        </Modal.Header>
        <Modal.Body>
        <Formik
      validationSchema={schema}
      onSubmit={(values, { setSubmitting }) => {
        const match = datas.find((data) => {
          return values.email === data.email && values.password === data.password;
        });
      
        if (match) {
          toast.success('Success');
          history.push('/invoice');
        } else {
          toast.error('Email and Password do not match');
        }
        setSubmitting(false);
      }}
      initialValues={{
        email: '',
        password: ''
      }}

    >
      {({ handleSubmit, handleChange, values, touched, errors }) => (
        <Form noValidate onSubmit={handleSubmit}>
          <Row className="mb-3 p-3">

            <Form.Group as={Col} md="12" controlId="validationFormik01" className='mb-4'>
              <Form.Label className='fw-bolder'>EMAIL ADDRESS</Form.Label>
              <Form.Control
                type="email"
                placeholder="email"
                autoFocus
                name="email"
                value={values.email}
                onChange={handleChange}
                isInvalid={touched.email && !!errors.email}

              />
              <Form.Control.Feedback type="invalid" >
                {errors.email}
              </Form.Control.Feedback>
            </Form.Group>
            <Form.Group as={Col} md="12" controlId="validationFormik02" className='mb-4'>
              <Form.Label>PASSWORD</Form.Label>
              <Form.Control
                autoFocus
                type="password"
                placeholder="password"
                name="password"
                value={values.password}
                onChange={handleChange}
                isInvalid={touched.password && !!errors.password}
              />
              <Form.Control.Feedback type="invalid">{errors.password}
              </Form.Control.Feedback>
            </Form.Group>
          </Row>
          <Button type="submit" className='w-100 px-3 mb-5' onClick={handleSubmit}>Login</Button>
          <div className='mb-5'>Don't have an account yet! <Link to="/login">Sign up!</Link></div>
        </Form>
      )}
    </Formik>
          
        </Modal.Body>
      </Modal>
   
      </>
  );
}

export default FormExample;