import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Login from './Login';
import './Navbar.css';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Home from './pages/Home';
import { Favorite } from '../shop/Favorite';
import { Shop } from '../shop/shop/shop';
import { Cart } from '../shop/cart/cart';
import { ShopContextProvider } from '../context/shop-context';
import Invoice from './Invoice';

function NavScrollExample() {
  const [loggedIn, setLoggedIn] = useState(false);
  const handleLogin = () => setLoggedIn(false);
  const handleLogout = () => setLoggedIn(true);
  return (
    <ShopContextProvider>
      <Router>
        <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" className="sticky-top">
          <Container>
            <Navbar.Brand href="#home">
              <img
                alt=".."
                src="https://media.discordapp.net/attachments/1098990936786227292/1105046607185059902/LogoMakr-0fwK60.png"
                width="80"
                height="80"
                className="d-inline-block align-center"
              />
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link as={Link} to="/" id="asd">
                  <Button variant="outline-light" className="px-3 border-0 h1 fs-4">
                    Home
                  </Button>
                </Nav.Link>
                <Nav.Link as={Link} to="/shop">
                  <Button variant="outline-light" className="px-3 border-0 h1 fs-4">
                    Products
                  </Button>
                </Nav.Link>
              </Nav>
              <Nav>
                <Nav.Link as={Link} to="/favorites">
                  <Button variant="outline-light" className="px-3 border-0 h1 fs-4" title="VIEW FAVORITE">
                    <i className="bi bi-heart" />
                  </Button>
                </Nav.Link>
                <Nav.Link as={Link} to="/cart">
                  <Button variant="outline-light" className="px-3 border-0 h1 fs-4" title="VIEW CART">
                    <i className="bi bi-cart3" />
                  </Button>
                </Nav.Link>
                {loggedIn ? (
                  <Nav.Link as={Link} to="/login" className="text-light" onClick={ handleLogin}>
                    <h5>Welcome User</h5> <Button variant="outline-light" className="px-3">
                      LOGOUT
                    </Button>
                  </Nav.Link>
                ) : (
                  <Nav.Link as={Link} to="/" className="text-light" onClick={handleLogout}>
                    <Button variant="outline-light" className="px-3">
                      SIGN IN
                      <div className="vr mx-3" />
                      SIGNUP
                    </Button>
                  </Nav.Link>
                )}
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        <Switch>
          <Route path="/invoice">
            <Invoice />
          </Route>
          <Route path="/shop">
            <Shop />
          </Route>
          <Route path="/favorites">
            <Favorite />
          </Route>
          <Route path="/login">
            <Login />
          </Route>
          <Route path="/cart">
            <Cart />
          </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Router>
      </ShopContextProvider>
    );
}

export default NavScrollExample;