import React, { useContext } from "react";
import { ShopContext } from "../context/shop-context";
import { PRODUCTS } from "../products";
import { FavoriteItem } from "../shop/Favorite-item";
import { Link } from "react-router-dom";


export const Favorite = () => {
  const { cartItems, getTotalCartAmount, checkout } = useContext(ShopContext);
  const totalAmount = getTotalCartAmount();

  return (
    <div className="text-center">
      <div className="py-5">
        <h1>Your Favorite Items</h1>
      </div>
      <div className="p-5">
        {PRODUCTS.map((product) => {
          if (cartItems[product.id] !== 0) {
            return <FavoriteItem data={product} />;
          }
        })}
      </div>

      {totalAmount > 0 ? (
        <div className="d-flex flex-column align-items-center">
          <Link to="/shop" className='btn btn-outline-danger text-dark w-25 text-decoration-none fw-bolder fs-3 mb-4'> Continue Shopping </Link>
        </div>
      ) : (
        <h1>You don't have any favorite item yet!</h1>
      )}

    </div>
  );
};