import React, { useContext } from "react";
import { ShopContext } from "../../context/shop-context";
import Form from 'react-bootstrap/Form';
import Table from 'react-bootstrap/Table';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export const CartItem = (props) => {
  const { id, productName, price, productImage } = props.data;
  const { updateCartItemCount, removeFromCart, getTotalCartAmount } = useContext(ShopContext);
  const totalAmount = getTotalCartAmount();

  return (
    <Container fluid>
      <Row>
        <Col sm={12}>
            <Table className="table table-borderless fs-4">
              <thead>
                <tr className="fs-4">
                  <th className="text-start">Product</th>
                  <th>Price</th>
                  <th>Quantity</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td className="text-start">
                    <img className="float-start me-5" style={{ height: '300px' }} src={productImage} alt="..." />
                    <p className="my-5 fs-5">{productName}</p>
                    <button className="px-5 py-2" onClick={() => removeFromCart(id)}>remove</button>
                  </td>
                  <td className="pt-5">Price: ${price}</td>
                  <td className="pt-5">
                    <Form.Select size="sm" className="w-25 mx-auto " onChange={(e) => updateCartItemCount(Number(e.target.value), id)}>
                      <option>1</option>
                      <option>2</option>
                      <option>3</option>
                      <option>4</option>
                    </Form.Select></td>
                  <td className="pt-5"> {totalAmount}</td>
                </tr>
              </tbody>
            </Table>
        </Col>
      </Row>
    </Container>

  );
};