import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
function ContactUs() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button className='border-0 bg-light text-dark p-0' onClick={handleShow}>
        ContactUs
      </Button>

      <Modal
        size="lg"
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title className='fw-bolder h1'>Contact Us</Modal.Title>
        </Modal.Header>
        <Modal.Body className='bg-dark text-light p-5'>
        <Container>
          <Row>
            <Col xs={12} md={12} className='p-5 bg-light text-dark rounded' style={{textAlign: 'justify'}}>
            <div className='fw-semibold mb-3'>
            We value your feedback and are here to assist you. If you have any questions, concerns, or suggestions, 
            please don't hesitate to reach out to our dedicated customer support team. Here are the two different ways you can contact us:
            </div>
            <div className='pb-3'>
            1. Customer Support Email: For general inquiries, order-related questions, or assistance, please email us at iamarkbrian7@gmail.com or reymark092720@gmail.com We strive to respond to all inquiries within 24-48 hours during business days.
            </div>
            <div>
            2. Social Media: Stay connected with us through our social media channels. You can find us on Facebook, Twitter, and Instagram. 
            Send us a direct message or leave a comment, and we'll get back to you as soon as possible.
            </div>
            </Col>
          </Row>
        </Container>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ContactUs;