import React from 'react';
import Card from 'react-bootstrap/Card';


export default function Def(props) {
  return (
    <Card style={{ width: '18rem' }} className='position-relative text-dark p-2'>
      <Card.Img
        style={{ height: 300 }}
        variant="top"
        src={props.coverImage} />
      <Card.Body>
        {props.openSpots === 0 && <div className='position-absolute top-0 start-0 border border-danger p-2 fw-bolder bg-primary rounded'>Best Seller!</div>}
        <Card.Text className='text-center'>
          <p className='fs-6' style={{textAlign: 'justify'}}>{props.description}</p>
        </Card.Text>
      </Card.Body>
    </Card>
  )
};
