import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import React, { useContext } from "react";
import { ShopContext } from "../context/shop-context";
export const FavoriteItem = (props) => {
   
    
    const { id, description, productName, productImage } = props.data;
    const { removeFromCart } =
      useContext(ShopContext);
  
  
    return (
      <Row className="justify-content-md-center">
        <Col xs lg="8">
        <div className="d-flex flex-row border fs-5 mb-5 py-5 px-5 ">
        
        <img style={{width: '250px'}} src={productImage} alt="..." />
        <div>
        <dl className='p-5'>
            <dt className="fw-bolder fs-1">{productName}</dt>
            <dd className="p fs-3">{description}</dd>
        </dl>
        <Button variant="outline-dark" className="px-3" onClick={() => removeFromCart(id)}>Remove</Button>
        </div>
      </div>
        </Col>
      </Row>
      
     
    );
  };
