import React, { useContext } from "react";
import { ShopContext } from "../../context/shop-context";
import Button from 'react-bootstrap/Button';

export const Product = (props) => {
  const { id, productName, price, productImage } = props.data;
  const { addToCart, addToFavorite } = useContext(ShopContext);



  return (
    <div className="mb-5">
      <div>
        <img id="trans" style={{width: '400px', height: '300px'}} className="border border-dark p-3 img-thumbnail border-5" alt="..." src={productImage} />
        <div className="mt-3">
          <p>
            <h4>{productName}</h4>
          </p>
          <p> {price}</p>
        </div>
        <div>
          <Button variant="outline-dark" onClick={() => addToCart(id)}>
            Add To Cart
          </Button>
          <Button variant="outline-dark ms-3" onClick={() => addToFavorite(id)}>Add to Favorite</Button>
        </div>
      </div>
    </div>
  );
};