import React, { useContext } from "react";
import { ShopContext } from "../context/shop-context";
import Form from 'react-bootstrap/Form';
import Table from 'react-bootstrap/Table';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
 const Invoiceitems = (props) => {
  const { id, description, productName, price, productImage } = props.data;
  const { addToCart, updateCartItemCount, getTotalCartAmount } = useContext(ShopContext);
  const totalAmount = getTotalCartAmount();

  return (
<Row>
      <Col className='mt-5'>
      <Table className="table table-borderless">
      <thead className="border-top">
        <tr className="fs-4">
        <th className="text-start">#</th>
          <th className="text-start">Product Name</th>
          <th>SKU</th>
          <th>Quantity</th>
          <th>Total</th>
        </tr>
      </thead>
      <tbody>
        <tr>
        <td className="pt-5">{id}</td>
        <td className="pt-5">{description}</td>
        <td className="pt-5">{productName}</td>
        <td className="pt-5">
          <Form.Select size="sm" className="w-25"   onChange={(e) => updateCartItemCount(Number(e.target.value), id)}>
            <option>1</option>
            <option>2</option>
            <option>3</option>
            <option>4</option>
          </Form.Select></td>
        <td className="pt-5">{totalAmount}</td>
        </tr>
        </tbody>
    </Table>
      </Col>
    </Row>
      );
    };
    export default Invoiceitems;