import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Accordion from 'react-bootstrap/Accordion';
function FAQ() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button className='border-0 bg-light text-dark p-0' onClick={handleShow}>
        FAQ's
      </Button>

      <Modal
        size="lg"
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title className='fw-bolder h1'>FAQs (Frequently Asked Questions)</Modal.Title>
        </Modal.Header>
        <Modal.Body className='bg-dark text-light p-5'>
          <Accordion defaultActiveKey="0" flush>
            <Accordion.Item eventKey="0">
              <Accordion.Header>1. What payment methods do you accept?</Accordion.Header>
              <Accordion.Body>
              We accept major credit cards such as Visa, Mastercard, American Express, and Discover. Additionally, we also offer the convenience of payment through popular online payment platforms like PayPal and Apple Pay.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="1">
              <Accordion.Header>2. How long does it take to process and ship an order?</Accordion.Header>
              <Accordion.Body>
              Once your order is placed, we typically process it within 1-2 business days. Shipping times vary depending on your location, but you can expect delivery within 5-10 business days for domestic orders and 10-15 business days for international orders. Please note that these estimates may vary during peak seasons and holidays.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="2">
              <Accordion.Header>3. Do you offer international shipping?</Accordion.Header>
              <Accordion.Body>
              Yes, we ship our t-shirts worldwide. Wherever you are located, you can enjoy our programming t-shirts. Please note that international customers may be subject to customs fees or import duties, which are the responsibility of the buyer.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="3">
              <Accordion.Header>4. Can I track my order?</Accordion.Header>
              <Accordion.Body>
              Absolutely! Once your order is shipped, we will provide you with a tracking number via email. You can use this tracking number to monitor the progress of your shipment and get real-time updates on its status.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="4">
              <Accordion.Header>5. What is your return and exchange policy?</Accordion.Header>
              <Accordion.Body>
              We want you to be completely satisfied with your purchase. If for any reason you are not happy with your programming t-shirt, we offer hassle-free returns and exchanges within 30 days of receiving your order. Please refer to our Return Policy page for detailed instructions on how to initiate a return or exchange.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="5">
              <Accordion.Header>6. How do I determine the right size for me?</Accordion.Header>
              <Accordion.Body>
              To help you choose the correct size, we provide a detailed sizing chart on each product page. We recommend measuring your favorite t-shirt and comparing it with the measurements provided in our chart to find the best fit. If you have any specific questions about sizing, feel free to reach out to our customer support team.              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="6">
              <Accordion.Header>7. Can I request a custom design or personalized t-shirt?</Accordion.Header>
              <Accordion.Body>
              At this time, we do not offer custom designs or personalized t-shirts. However, we are constantly expanding our product range, so keep an eye out for future updates and new offerings.
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item eventKey="7">
              <Accordion.Header>8. Do you offer wholesale or bulk orders?</Accordion.Header>
              <Accordion.Body>
              Yes, we offer wholesale and bulk order options. If you are interested in purchasing programming t-shirts in larger quantities, please reach out to our wholesale team through the Contact Us page. We will be happy to assist you and provide the necessary information.
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default FAQ;