import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
function About() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button className='border-0 bg-light text-dark p-0' onClick={handleShow}>
        ABOUT US
      </Button>

      <Modal
        size="xl"
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title className='fw-bolder h1'>ABOUT US </Modal.Title>
        </Modal.Header>
        <Modal.Body className='bg-dark text-light'>
        <Container>
          <Row className='p-5'>
            <Col xs={12} md={12} className=' bg-light text-dark rounded' style={{textAlign: 'justify'}}>
            <div className='fw-bold text-center h3 my-3'>Welcome to BR Clothing!</div> 
<div className='fs-5 px-4 p-5' style={{textAlign: 'justify'}}>
At BR Clothing, we're passionate about programming and expressing our love for coding through stylish and creative t-shirts. Our mission is to provide programmers, developers, and tech enthusiasts with high-quality, unique t-shirts that showcase their passion for programming languages, algorithms, and everything tech-related.

What sets us apart?

Quality Materials: We believe in delivering the best products to our customers. That's why we carefully select premium fabrics and use top-notch printing techniques to ensure durability and vibrant colors in every t-shirt.

Innovative Designs: Our team of talented designers creates original, eye-catching designs that capture the essence of programming and technology. From clever code snippets to witty programming jokes, our t-shirts are sure to bring a smile to your face.

Wide Range of Options: We understand that every programmer has their own unique style and preferences. That's why we offer a diverse range of designs, sizes, and colors to cater to everyone's taste. Whether you prefer minimalist designs or bold statements, we have something for you.

Customer Satisfaction: Your satisfaction is our top priority. We strive to provide excellent customer service, ensuring a smooth and enjoyable shopping experience. If you have any questions or concerns, our friendly support team is always ready to assist you.

Express Your Passion: We believe that programming is more than just a career; it's a passion. Our t-shirts allow you to express your love for coding and technology in a fashionable way. Wear them proudly and let the world know about your programming prowess.

Join our community of programmers and tech enthusiasts today! Explore our collection of programming t-shirts and find the perfect one that resonates with your coding journey.

Happy coding and happy shopping!
</div>
            </Col>
          </Row>
        </Container>
           
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default About;